import React, { useContext, useState } from 'react'
import KeyPassages from './../../data/KeyPassages.json'
import styles from './Leaf.module.css'
import AppContext from '../../contextAPI/AppContext'
import Popper from '@material-ui/core/Popper';
import Typography from '@material-ui/core/Typography';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import Foundational from './../../images/Foundational.png'
import Celebration from './../../images/Celebration.png'
import Communication from './../../images/Communications.png'
import Economics from './../../images/Economics.png'
import Education from './../../images/Education.png'
import Family from './../../images/Family.png'
import Government from './../../images/Government.png'
import Religion from './../../images/Religion.png'

import { makeStyles } from '@material-ui/core/styles';

const highlightColors = {
    CEL: '#f9d7fb',
    COM: '#eed7fb',
    ECO: '#fbe9d7',
    EDU: '#d7fbf8',
    FAM: '#fbcaca',
    GOV: '#fafbca',
    REL: '#d8fbca',
}
const useStyles = makeStyles(theme => ({
    typography: {
      padding: theme.spacing(2),
      fontSize: 'small',
      color: 'white'
    },
    paper: {
        background: '#3A4C7D',
        width: 'fit-content',
        maxWidth: '60vw'
    },
    logo: {
        height: '30px',
        width: '30px',
        marginRight: '20px',
        flex: '0 0 30px'
    },
    leaf: {
        whiteSpace: 'pre-wrap',
        height: '25px',
    },
    chapter: {
        fontWeight: 'bold'
    },
    verse: {
        verticalAlign: 'top',
        position: 'relative',
        top: '-0.3em',
        fontSize: '60%',
        backgroundColor: 'transparent'
    },
    dialogRef: {
        color: 'grey',
        fontSize: '80%',
        textAlign: "center",
        marginBottom: '-15px'
    },
    dialogHeader: {
        display: 'flex',
        justifyContent: 'flex-start',
        marginBottom: '-10px'
    }
  }));

const SphereLeaf = (props) => {
    // This controls the visibility of chapter and verse references
    const receivedAppContext = useContext(AppContext);
    const [anchorEl, setAnchorEl] = useState(null);
    const classes = useStyles();

    // This controls the popup for the word information
    const [ dialog, updateDialog ] = useState({open: false, content: {}})
    const newDialog = (obj) => {
        updateDialog(obj)
    }

    // Create CSS styles from marks
    const keyPassage = (props.marks) ? props.marks.find(mark => mark.type === 'keyPassage') : undefined
    const sphere = (keyPassage) ? keyPassage.data.id.substring(0.3) : null
    const keyPassageNum = (keyPassage) ? keyPassage.data.id.substring(4,6) : null
    const doubleHighlight = parseInt(keyPassageNum) > 46
    const chapter = (props.marks) ? props.marks.find(mark => mark.type === 'chapter') : undefined
    const verse = (props.marks) ? props.marks.find(mark => mark.type === 'verse') : undefined
    const footnote = (props.marks) ? props.marks.find(mark => mark.type === 'footnote') : undefined
    const bibleRef = (verse || chapter) ? props.marks.find(mark => mark.type === 'reference') : undefined
    let classNames = []
    if (verse !== undefined) {
        classNames.push(classes.verse)
    }
    if (chapter !== undefined) {
        classNames.push(classes.chapter)
    }

    let styledClasses = ''

    classNames.map(c => {
        if (classNames.indexOf(c) === 0) {
            styledClasses = `${c}`
        } else {
            styledClasses = styledClasses + `, ${c}`
        }
    })

    let highlight = ''
    if (keyPassage && doubleHighlight) {
        highlight = `repeating-linear-gradient(${highlightColors[KeyPassages[keyPassage.data.id].spheres[0]]}, ${highlightColors[KeyPassages[keyPassage.data.id].spheres[0]]} 49.9%, ${highlightColors[KeyPassages[keyPassage.data.id].spheres[1]]} 50.1%, ${highlightColors[KeyPassages[keyPassage.data.id].spheres[1]]} 100%)`
    } else if (keyPassage && !doubleHighlight) {
        highlight = highlightColors[KeyPassages[keyPassage.data.id].spheres[0]]
    } else {
        highlight = ''
    }

    let backgroundColor = '' 
    if (keyPassage && !doubleHighlight) {
        backgroundColor = highlightColors[KeyPassages[keyPassage.data.id].spheres[0]]
    } else if (keyPassage && doubleHighlight) {
        backgroundColor = highlightColors[KeyPassages[keyPassage.data.id].spheres[1]]
    } else {
        backgroundColor = ''
    }

    function getSphereLogo(SPH) {
        const sphere = SPH.substring(0,3)
        switch (sphere) {
            case 'FOU':
                return <img src={Foundational} className={classes.logo} />
            case 'CEL':
                return <img src={Celebration} className={classes.logo} />
            case 'COM':
                return <img src={Communication} className={classes.logo} />
            case 'ECO':
                return <img src={Economics} className={classes.logo} />
            case 'EDU':
                return <img src={Education} className={classes.logo} />
            case 'FAM':
                return <img src={Family} className={classes.logo} />
            case 'GOV':
                return <img src={Government} className={classes.logo} />
            case 'REL':
                return <img src={Religion} className={classes.logo} />
        }
    }
    
    function handleClick(event) {
        setAnchorEl(anchorEl ? null : event.currentTarget);
      }
    
    function handleClickAway() {
        setAnchorEl(null)
    }

    const open = Boolean(anchorEl);
    // If it's just a chapter/verse reference then return that leaf with a reference
    if (verse || chapter) {
        return (
            <span style={{
                background: highlight,
                boxShadow: (backgroundColor) ? `0px 1px 0px 0px ${backgroundColor}` : '',
                whiteSpace: 'pre-wrap',
                height: '25px',
            }}
            onClick={() => newDialog({ open: !dialog.open, content: KeyPassages[keyPassage.data.id] })}
            >
            <span 
                id={`${bibleRef.data.book}-${bibleRef.data.chapter}-${bibleRef.data.verse}`}
                className={styledClasses} 
                style={{
                    display: (receivedAppContext.displayRef) ? '' : 'none',
                    background: highlight,
                    boxShadow: (backgroundColor) ? `0px 1px 0px 0px ${backgroundColor}` : ''
                }}
                >
                    {props.text}{(chapter)?'':'\u00A0'}
                </span></span>)
    }
    // If not a verse, don't create a reference
     else {
        return (<span style={{
            background: highlight,
            boxShadow: (backgroundColor) ? `0px 1px 0px 0px ${backgroundColor}` : '',
            whiteSpace: 'pre-wrap',
            height: '25px',
        }}
        onClick={() => newDialog({ open: !dialog.open, content: KeyPassages[keyPassage.data.id] })}
        >
            {footnote ? (
                <span className={styledClasses} onClick={handleClick} onMouseOver={handleClick} onMouseLeave={handleClick}>
                    {props.text}
                    <Popper id="footnote" open={open} anchorEl={anchorEl} transition placement="top">
                        {({ TransitionProps }) => (
                            <ClickAwayListener onClickAway={handleClickAway}>
                                <Fade {...TransitionProps} timeout={350}>
                                    <Paper className={classes.paper}>
                                    <Typography className={classes.typography}>{footnote.data.footnote}</Typography>
                                    </Paper>
                                </Fade>
                            </ClickAwayListener>
                        )}
                    </Popper>
                </span>
            ) : <span className={styledClasses}>{props.text}</span>}
            {(dialog.open === true) ? (
                <Dialog 
                maxWidth='lg'
                    open={dialog.open} 
                    onClose={() => newDialog({open: false, content: {}})}>
                    <DialogTitle>
                        <div className={classes.dialogHeader}>
                            {getSphereLogo(sphere)}
                            <p className={classes.dialogRef}>{dialog.content.mainSphere} #{keyPassage.data.id.substring(4,6)}</p>
                        </div>
                        {dialog.content.title}
                    </DialogTitle>
                    <DialogContent dividers>
                        <p>Let's Think About This:</p>
                        <ul>
                            <li>{dialog.content.questions.Q1}</li>
                            <li>{dialog.content.questions.Q2}</li>
                            <li>{dialog.content.questions.Q3}</li>
                            <li>{dialog.content.questions.Q4}</li>
                        </ul>
                    </DialogContent>
                </Dialog> 
            ) : null}
        </span>
        )
     }
     
    }

export default SphereLeaf;