import React, { useContext } from 'react'
import AppContext from '../../contextAPI/AppContext'
import styles from './WordDataTable.module.css'

const Table = (props) => {
        const receivedAppContext = useContext(AppContext)
        const displayedData = (receivedAppContext.language === 'GRK') ? [
                ['Gloss', props.content.IT],
                ['Lemma', props.content.lexeme],
                ['SBL Transliteration', props.content.transSBL],
                ['GK #', props.content.GoodrickKohlenbergerNumbers],
                ['LouwNida #', props.content.LouwNidaNumbers]
         ] : [
                ['Gloss', props.content.ETCBCgloss],
                ['Lemma', props.content.HebrewLexeme],
                ['SBL Transliteration', props.content.SBLstyleTransliteration],
                ['Hebrew Reference', `${props.content.Heb_ref.book_id} ${props.content.Heb_ref.chapter_id}:${props.content.Heb_ref.verse_id}`],
                ['KJV Reference', `${props.content.KJV_ref.book_id} ${props.content.KJV_ref.chapter_id}:${props.content.KJV_ref.verse_id}`],
                ['Morph Details', props.content.morphologyDetail]
         ]
        return (
        <table className={styles.dialogTable}><tbody>
                {displayedData.map(row => (<tr className={styles.row}><td className={styles.leftCell}>{row[0]}</td><td className={styles.rightCell}>{(typeof row[1] === "object")?`${row[1].book_id} ${row[1].chapter_id}:${row[1].verse_id}`:row[1]}</td></tr>))}
                </tbody>
        </table>
        )
}

export default Table;