import React, { useContext, useState } from 'react'
import styles from './SourceName.module.css'
import AppContext from '../../contextAPI/AppContext'
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import SourceViewInfo from '../SourceViewInfo/SourceViewInfo'

const SourceName = (props) => {
    // const receivedAppContext = useContext(AppContext)
    
    // This controls the popup for the source information
    const [ sourceDialog, updateSourceDialog ] = useState({open: false, content: {}})
    const newSourceDialog = (obj) => {
        updateSourceDialog(obj)
    }
    return (
    <p className={styles.sourceName}
    onClick={() => newSourceDialog({open: !sourceDialog.open, content: props.data})}
    >
        {props.sourceName}
        {(sourceDialog.open === true) ? (
            <Dialog 
                maxWidth='lg'
                open={sourceDialog.open}
                onClose={() => newSourceDialog({open: false, content: {}})}>
                    <DialogTitle>SourceView Info</DialogTitle>
                    <DialogContent dividers>
                    <SourceViewInfo content={sourceDialog.content}/>  
                    </DialogContent>
            </Dialog> 
        ) : null}
    </p>
)}

export default SourceName;