import React, { useEffect, useContext, useState } from 'react'
import AppContext from '../contextAPI/AppContext'
import scrollToElement from 'scroll-to-element'
import SpeechBubble from '../components/SpeechBubble/SpeechBubble'
import Introduction from '../components/Introduction/Introduction'
import Narrator from '../components/NarratorText/NarratorText'
import SegmentData from '../components/SegmentData/SegmentData'
import ReadTogether from '../images/SVB-Read-together.png'
import DiscussTogether from '../images/SVB-Discuss-together.png'
import SegmentTitles from '../data/SegmentTitles.json'
import QuestionsRefs from '../data/QuestionsRefs.json'
import QuestionsData from '../data/QuestionsData(Eng).json'
import Button from '@material-ui/core/Button';

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  typography: {
    padding: theme.spacing(2),
    fontSize: 'small',
    color: 'white'
  },
  paper: {
      background: '#3A4C7D',
      width: '75vw'
  },
  segmentDataContainer: {
    display: 'flex',
    justifyContent: 'center',
    margin: '10px 0',
    padding: '10px 0',
  },
  header: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    paddingTop: '5px'
  },
  title: {
    fontSize: '150%',
    fontWeight: ' bold',
    textDecoration: 'underline',
    width: '100%',
    margin: 0,
    textAlign: 'center'
  },
  reference: {
    margin: 0,
    fontSize: '60%',
    color: 'grey'
  },
  discuss: {
    display: 'block',
    textAlign: 'center',
    margin: '10px 0',
    padding: '10px 0',
    borderTop: '0.25px lightgrey solid',
  },
  footer: {
    textAlign: 'center',
    fontSize: '60%',
    margin: '10px 0 30px 0',
    '& p': {
      margin: 0
    }
  },
  images: {
    width: '200px',
    margin: '10px auto',
  },
  readImage: {
    marginBottom: '20px',
    borderBottom: '0.25px lightgrey solid',
  },
  content: {
    margin: '0 0 10px 0',
    padding: '0 0 10px 0'
  },
  questions: {
    textAlign: 'left',
    margin: '30px 0',
    '& p': {
      margin: '10px 0',
    }
  },
  imgCaption: {
    color: 'grey',
    fontSize: '70%',
    margin: '-20px 0 0 0',
    textAlign: 'center'
  }
}))


export default ({pageContext: {segment, book, segmentID}}) => {
  const classes = useStyles();
  // figure out solution for introductions without QRefs 
  const [ questionRef, updateQuestionRef ] = useState((segmentID[0] === 'S') ? QuestionsRefs[segmentID].QRef1 : '')
  const receivedAppContext = useContext(AppContext);
  const NTBooks = ["Tit", "Rev", "Jam", "3Jn", "Heb", "2Co", "Luk", "Mat", "2Ti", "2Th", "1Pe", "Gal", "2Pe", "Joh", "Php", "1Ti", "Phm", "Mar", "Eph", "Act", "1Co", "Col", "Jud", "Rom", "1Jn", "2Jn", "1Th"]
  const isNT = (NTBooks.indexOf(book) !== -1) ? true : false
  
  function changeQuestions () {
    if (questionRef === QuestionsRefs[segmentID].QRef1) {
      updateQuestionRef(QuestionsRefs[segmentID].QRef2)
    } else if (questionRef === QuestionsRefs[segmentID].QRef2) {
      updateQuestionRef(QuestionsRefs[segmentID].QRef3)
    } else if (questionRef === QuestionsRefs[segmentID].QRef3) {
      updateQuestionRef(QuestionsRefs[segmentID].QRef1)
    }
  }
  useEffect(() => {
    if (typeof window !== undefined) {
      
      if (receivedAppContext.searchID !== '') {
        const id = `#${receivedAppContext.searchID}`
        scrollToElement(id, {
            offset: -65,
            ease: 'inOutCube',
            duration: 1500
        });
        receivedAppContext.updateSearchID('')
      }
    }
  }, [])

  const currentYear = new Date().getFullYear()

  return (
    <div>
      <div className={classes.header}>
        <p className={classes.title}>{SegmentTitles[segmentID].title}</p>
        {(segmentID[0] === 'S') ? <p className={classes.reference}>{(SegmentTitles[segmentID].book.length > 1) ?
           `${SegmentTitles[segmentID].book[0]} ${SegmentTitles[segmentID].ref.split('-')[0]} - ${SegmentTitles[segmentID].book[1]} ${SegmentTitles[segmentID].ref.split('-')[1]}`
          : `${SegmentTitles[segmentID].book[0]} ${SegmentTitles[segmentID].ref}`}</p> : null}
      </div>
      {/* segments start with an array of block nodes (so they have a length), but intros start with an object "document"  */}
      {(segmentID[0] === 'S') ? (
        <div>
          <div className={classes.segmentDataContainer}>
            <SegmentData 
              colors={segment.colors}
              sources={segment.sources}
              repeatedWords={segment.repeatedWords}
            />
          </div>
          <div className={`${classes.images} ${classes.readImage}`}>
            <img src={ReadTogether} alt="Read Together" />
            <p className={classes.imgCaption}>Each person reads a color aloud</p>
          </div>
          <div className={classes.content}>
            {segment.content.map(block => {
              if (block.data.color === 'black') {
                return (<Narrator 
                  sourceData={block.data}
                  inlines={block.nodes} />
                  )
              } else {
                return (<SpeechBubble 
                  sourceData={block.data}
                  inlines={block.nodes} />
                  )
              }
            })}
          </div>
          <div className={classes.discuss}>
            <div className={classes.images}>
              <img src={DiscussTogether} alt="Discuss Together" />
              <p className={classes.imgCaption}>Discuss the questions with others</p>
            </div>
            <p className={classes.title}>{QuestionsData[questionRef].title}</p>
            <div className={classes.questions}>
              <p>{QuestionsData[questionRef].Q1}</p>
              <p>{QuestionsData[questionRef].Q2}</p>
              <p>{QuestionsData[questionRef].Q3}</p>
              <p>{QuestionsData[questionRef].Q4}</p>
            </div>
            <Button color='secondary' variant="contained" onClick={() => changeQuestions()}>Change Questions</Button>
          </div>
          <div className={classes.footer}>
            <p>Copyright info of version</p>
            <p>© {currentYear} SourceView LLC.</p>
          </div>
        </div>
    ) : (
        <Introduction 
          content={segment.content.document.nodes} 
          // bookData={segment.bookData}
          testament={isNT ? 'NT' : 'OT'}/>
      )}
    </div>
  )}