import React from 'react'
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import { Doughnut } from 'react-chartjs-2'

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  main: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    textAlign: 'center',
    marginBottom: '8px',
    maxHeight: 'fit-content',
  },
  card: {
    maxHeight: '150px',
    margin: '5px',
  },
  leftCard: {
    marginRight: '8px'
  },
  top5: {
    flex: 1,
    textAlign: 'center',
    fontSize: '75%',
    padding: 0
  },
  cardTitle: {
    margin: '0',
    textDecoration: 'underline',
    fontWeight: 'bold',
    fontSize: '80%',
    padding: '2px'
  },
  top5List: {
    padding: '1px',
    margin: 1,
  },
  cardContent: {
    margin: 0,
    padding: 0,
    fontSize: '11px',
    lineHeight: '13px'
  },
  backgroundColor: {
    backgroundColor: 'rgba(58, 76, 125, 0.07)'
  },
  red: {
    color: '#d60000'
  },
  green: {
    color: '#006400'
  },
  blue: {
    color: 'darkblue'
  },
  topSources: {
    flex: '1 100px'
  },
  topWords: {
    flex: '1 100px'
  },
  colorComparison: {
    flex: '1 1 300px',
  }
})

const createPieChartData = (obj) => {
  return {
    labels: obj.percentage_labels,
    datasets: [{
      data: obj.percentage_values,
      backgroundColor: [
        'rgb(209, 0, 209)',
        'rgb(255, 238, 4)',
        'lightgrey',
        ],
      hoverBackgroundColor: [
        'purple',
        'rgb(184, 171, 0)',
        'grey'
        ]
    }]
  }
}

const chartOptions = {
  responsive: true,
  maintainAspectRatio: true,
  layout: {
    padding: {
      left: 10,
      right: 10,
      top: 0,
      bottom: 50
    }
  },
  legend: {
    position: 'right',
    labels: {
      fontSize: 9,
      boxWidth: 40
    }
    // display: false
  },
}

const SegmentData = (props) => {
    const classes = useStyles();
    return (
      <div className={classes.main}>
        <Card className={`${classes.backgroundColor} ${classes.card} ${classes.colorComparison}`}>
          <CardHeader title={(props.testament === 'NT') ? 'Quotes and Allusions from OT' : 'Quotes and Allusions used in NT'} disableTypography="true" className={classes.cardTitle}/>
          <CardContent className={`${classes.cardContent} ${classes.chart}`}>
            <Doughnut data={createPieChartData(props.pieData)} options={chartOptions} />
          </CardContent>
        </Card>
        <Card className={`${classes.top5} ${classes.leftCard} ${classes.backgroundColor} ${classes.card}  ${classes.topSources}`}>
          <CardHeader title="Books with Quotes and Allusions" disableTypography="true" className={classes.cardTitle}/>
          <CardContent className={classes.cardContent}>
          BarChart with books
          </CardContent>
        </Card>
        <Card className={`${classes.top5} ${classes.backgroundColor} ${classes.card} ${classes.topWords}`}>
            <CardHeader title="Cross References" disableTypography="true" className={classes.cardTitle}/>
            <CardContent className={classes.cardContent}>
            <p>List of hyperlinks</p>
          </CardContent>
        </Card>
      </div>
)}

export default SegmentData;