import React from 'react'
import styles from './SourceViewInfo.module.css'

const SourceViewInfo = (props) => {
    const SVBData = props.content
    const color = () => {
        switch (SVBData.color) {
            case 'blue':
                return 'darkblue'
            case 'green':
                return '#006400'
            case 'red':
                return '#d60000'
            case 'black':
                return 'black'
            default:
                return 'black'
        } 
    }

    if (SVBData.color !== 'black') {
        let recipList = ``
        SVBData.unique_recipients.map(recip => {
            recipList = (recipList !== '') ? recipList + `, ${recip}` : recipList + `${recip}`
        })
        return (
            <div>
                <table className={styles.SVBInfoTable}>
                    <tbody>
                        <tr className={styles.row}>
                            <td className={styles.leftCell}><i className={`pi pi-user`} style={{'fontSize': '3em'}}></i></td>
                            <td className={styles.middleCell}><i className={`pi pi-arrow-circle-right`} style={{'fontSize': '3em', 'color': color()}}></i></td>
                            <td className={styles.rightCell}><i className={`pi pi-users`} style={{'fontSize': '3em'}}></i></td>
                        </tr>
                        <tr className={styles.row}>
                            <td className={styles.leftCell}><span className={styles.cellTitle}>SOURCE(S)</span><br />{SVBData.source}</td>
                            <td className={styles.middleCell}></td>
                            <td className={styles.rightCell}><span className={styles.cellTitle}>RECIPIENT(S)</span><br />{(SVBData.unique_recipients.length === 1) ? SVBData.recipient : recipList}</td>
                        </tr>
                        <tr className={styles.row}>
                            <td className={styles.leftCell}>Historical Time Period: </td>
                            <td className={styles.middleCell}></td>
                            <td className={styles.rightCell}>{SVBData.time_period[0]}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        )
    } else {
        return (<div className={styles.narrator}>
                <i className={`pi pi-user`} style={{'fontSize': '3em'}}></i>
                <p className={styles.cellTitle}>SOURCE</p>
                <p className={styles.sourceName}>{`${SVBData.source} (${SVBData.unique_sources[0]})`}</p>
                <table className={styles.SVBInfoTable}>
                    <tbody>
                        <tr className={styles.row}>
                            <td className={styles.leftCell}>Historical Time Period: </td>
                            <td className={styles.middleCell}></td>
                            <td className={styles.rightCell}>{SVBData.time_period[0]}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        )
    }
}

export default SourceViewInfo;